/* Components */
.button {
  position: relative;
  display: flex;
  align-items: stretch;
  background-color: transparent;
  color: inherit;
  border: none;
  padding: 0;
  border-radius: 2px;
  cursor: pointer;
  outline: none;
}
.button__bar--outer {
  width: 100%;
  height: 4px;
  bottom: -4px;
  left: 0;
  position: absolute;
}
.button__bar--inner {
  background: white;
  width: 100%;
  height: 100%;
  transform: scale3d(1, 1, 1);
  transform-origin: left;
  transition: transform cubic-bezier(0.19, 1, 0.22, 1) 0.8s;
}
.button__item {
  border: solid 2px;
  padding: 16px 24px;
  display: flex;
  align-items: center;
}
.button__icon {
  border-left-style: none;
}
.button--icon-only .button__icon {
  border-left-style: solid;
}
.button:hover .button__item {
  text-decoration: underline;
}
.button:hover .button__bar--inner {
  transform: scale3d(0, 1, 1);
  transform-origin: right;
}
.button--text-link {
  margin: 0 32px;
}
.button--text-link .button__bar--inner {
  transform: scale3d(0, 1, 1);
  transform-origin: right;
}
.button--text-link .button__item {
  border: none;
  padding-left: 0;
  padding-right: 0;
}
.button--text-link:hover .button__item {
  text-decoration: none;
}
.button--text-link:hover .button__bar--inner {
  transform: scale3d(1, 1, 1);
  transform-origin: left;
}
.active .button .button__bar--inner {
  transform: scale3d(1, 1, 1);
  transition: none;
}

